.social-list {
  .reset-list();
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 4px;

  @media (max-width: @mobile-width-only) {
    margin-bottom: 35px;
  }

  // @media (min-width: @tablet-width) {
  //   margin-left: 55px;
  // }
}

.social-list__link {
  display: flex;
  margin-right: 6px;
  width: 46px;
  height: 48px;
  background-color: @light-blue;
  justify-content: center;
  align-items: center;

  &:hover {
    background-color: @basic-blue;
  }

  &:active {
    background-color: @dark-blue;
  }
}

.social-list__icon {
  fill: @basic-white;

  &:active {
    opacity: 0.3;
  }
}
