.lead {
  position: relative;
}

.lead br:not(:nth-child(2)) {
  @media (min-width: @tablet-width) {
    display: none;
  }
}

.lead__wrapper {
  text-align: center;

  @media (max-width: @mobile-width-only) {
    padding: 0 18px;
    width: 320px;
    margin: 0 auto;
    box-sizing: border-box;
    // display: flex;
    // align-items: center;
    // flex-direction: column;
  }

  @media (min-width: @tablet-width) {
    width: 768px;
    margin: 0 auto;
    padding: 0 124px;
    box-sizing: border-box;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;
  }
}

.lead__wrapper--catalog {
  @media (min-width: @tablet-width) {
    padding: 0 170px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 380px;
  }
}

.lead__wrapper--form {
  @media (min-width: @tablet-width) {
    padding: 19px 165px 0 165px;
  }

  @media (min-width: @desktop-width) {
    padding: 56px 380px 0 380px;
  }
}

.lead__headline {
  margin: 0;

  @media (max-width: @mobile-width-only) {
    padding: 34px 0 33px 0;
  }

  @media (min-width: @tablet-width) {
    padding: 44px 0 35px 0;
  }

  @media (min-width: @desktop-width) {
    padding: 57px 0 32px 0;
  }
}

.lead__headline--catalog {
  @media (max-width: @mobile-width-only) {
    padding: 27px 0 26px 0;
  }

  @media (min-width: @tablet-width) {
    padding: 60px 0 35px 0;
    font-size: 30px;
  }

  @media (min-width: @desktop-width) {
    padding: 101px 0 32px 0;
  }
}

.lead__text {
  margin: 0;
  line-height: 24px;

  @media (max-width: @mobile-width-only) {
    padding-bottom: 30px;
  }

  @media (min-width: @tablet-width) {
    padding-bottom: 50px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 39px;
  }
}

.lead__text-form_br {
  display: none;

  @media (max-width: @mobile-width-only) {
    display: block;
  }
}
