.button {
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  color: @basic-white;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
}

.button--like {
  background: url("../img/icon-like.svg") no-repeat center;
  width: 23px;
  height: 21px;

  @media (min-width: @desktop-width) {
    background: url("../img/like-icon-1200.svg") no-repeat center;

    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }
}
