.field__text-place {
  padding: 18px 22px 14px 22px;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  color: @basic-black;
  border: none;
  background-color: @light-grey;
  width: 100%;
  box-sizing: border-box;

  &::placeholder {
    color: @basic-black;
  }

  &:hover {
    background-color: @catalog-after-grey;
  }

  &:focus {
    background-color: @basic-white;
    border: 4px solid @catalog-after-grey;
    padding: 14px 18px 10px 18px;
  }
}
