.contacts-form {
  position: relative;

  @media (max-width: @mobile-width-only) {
    // margin: 20px 0 0 0;
  }

  @media (min-width: @tablet-width) {
    grid-column: ~"1/3";

    &::before {
      content: "";
      position: absolute;
      width: 123px;
      height: 1px;
      background-color: @form-before-grey;
      top: -35px;
      left: 0;
    }

    &::after {
      content: "";
      position: absolute;
      width: 123px;
      height: 1px;
      background-color: @form-before-grey;
      top: -35px;
      right: 0;
    }
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;

    &::before {
      width: 280px;
      top: -87px;
    }

    &::after {
      width: 280px;
      top: -87px;
    }
  }
}

.contacts-form__title {
  text-align: center;

  @media (max-width: @mobile-width-only) {
    padding: 20px 0 12px 0;
  }

  @media (min-width: @tablet-width) {
    padding-bottom: 15px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 71px;
  }
}

