.contacts-form__field {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    background-color: @basic-grey;
    width: 58px;
    height: 58px;
    top: 30px;
    right: 0;
  }

  @media (max-width: @mobile-width-only) {
    padding-bottom: 14px;

    &:last-of-type {
      padding-bottom: 28px;
    }
  }

  @media (min-width: @tablet-width) {
    padding-bottom: 25px;

    &:last-of-type {
      padding-bottom: 52px;
    }
  }

  @media (min-width: @desktop-width) {
    display: flex;
    align-items: center;
    padding-bottom: 52px;

    &::after {
      top: 0;
      right: 0;
    }
  }
}

.contacts-form__field--telephone {
  &::after {
    background: @basic-grey url("../img/icon-phone.svg") no-repeat center;
  }

  @media (max-width: @mobile-width-only) {
    &::after {
      top: 38px;
    }
  }

  @media (min-width: @tablet-width) {
    &::after {
      top: 40px;
    }
  }

  @media (min-width: @desktop-width) {
    &::after {
      top: 0;
      right: 78px;
    }
  }
}

.contacts__form__field--email {
  &::after {
    background: @basic-grey url("../img/icon-mail.svg") no-repeat center;
  }

  @media (max-width: @mobile-width-only) {
    &::after {
      top: 32px;
    }
  }

  @media (min-width: @tablet-width) {
    &::after {
      top: 34px;
    }
  }

  @media (min-width: @desktop-width) {
    &::after {
      top: 0;
    }
  }
}

.contacts-form__label {
  padding-left: 20px;
  display: block;

  @media (max-width: @mobile-width-only) {
    padding-bottom: 7px;
  }

  @media (min-width: @tablet-width) {
    padding-bottom: 8px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 18px 0 0;
  }
}

.contacts-form__desktop-wrapper {
  @media (min-width: @desktop-width) {
    width: 315px;
  }
}
