.player__band::before {
  content: "";
  position: absolute;
  width: 235px;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.2);
  top: -22px;
  left: 22px;
}
.player__band::after {
  content: "";
  position: absolute;
  width: 9px;
  height: 9px;
  border-radius: 50%;
  background-color: #ffffff;
  top: -25px;
  left: 58px;
}
@media (min-width: 768px) {
