.booking {
  @media (max-width: @mobile-width-only) {
    width: 320px;
    margin-bottom: 38px;
  }
  margin: 0 auto;
  padding: 33px 20px 10px 20px;
  text-align: center;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    width: 768px;
    margin: 0 auto;
    padding: 57px 155px;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;
    padding: 58px 155px 65px 155px;
  }
}

.booking__title {
  margin: 0;
  margin-bottom: 22px;
  font-size: 21px;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    margin-bottom: 35px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 32px;
  }
}

.booking__text {
  margin: 0;
  margin-bottom: 26px;
  line-height: 24px;

  @media (min-width: @tablet-width) {
    margin-bottom: 37px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 43px;
  }
}

.booking__br-text {
  @media (max-width: @mobile-width-only) {
    display: none;
  }
}

.booking__button {
  padding: 15px 35px 18px 35px;
  background-color: @light-blue;

  @media (min-width: @tablet-width) {
    padding: 15px 124px 18px 124px;
  }
}

.booking__map {
  display: block;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    width: 27px;
    height: 27px;
    background: url("../img/map-marker.svg") no-repeat center;
    top: 140px;
    left: 60%;
  }

  @media (max-width: @mobile-width-only) {
    width: 320px;
    margin: 0 auto;
  }

  @media (min-width: @tablet-width) {
    width: 768px;
    margin: 0 auto;

    &::after {
      left: 54%;
    }
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;

    &::after {
      left: 53%;
    }
  }
}
