.main-nav {
  position: relative;
}

.main-nav__wrapper {
  @media (min-width: @tablet-width) {
    width: 768px;
    margin: 0 auto;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;
  }
}

.main-nav__menu-list {
  .reset-list();
  display: flex;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    flex-direction: row;
  }
}

.main-nav__item {
  @media (max-width: @mobile-width-only) {
    background-color: @light-blue;
    font-size: 18px;
    line-height: 40px;
    font-weight: 700;
    padding-top: 10px;
    padding-bottom: 5px;
    text-align: center;
    text-transform: uppercase;
    border-bottom: 1px solid @border-black;
    border-top: 1px solid @clear-white;

    &:first-of-type {
      border-top: none;
    }

    &:last-child {
      border-bottom: none;
      padding-bottom: 3px;
    }

    &:hover {
      background-color: @basic-blue;
    }

    &:active {
      background-color: @dark-blue;
    }
  }

  @media (min-width: @tablet-width) {
    font-size: 14px;
    line-height: 26px;
    font-weight: 700;
    text-align: center;
    padding: 16px 10px 14px 10px;
    width: 153px;
    box-sizing: border-box;

    &:nth-child(3) {
      margin-left: 156px;
    }
  }

  @media (min-width: @desktop-width) {
    width: 239px;

    &:nth-child(3) {
      margin-left: 244px;
    }
  }
}

.main-nav__link {
  @media (max-width: @mobile-width-only) {
    color: @basic-white;

    &:active {
      opacity: 0.3;
    }
  }

  @media (min-width: @tablet-width) {
    color: @basic-black;

    &:hover {
      color: @light-blue;
    }

    &:active {
      color: @clear-black;
    }
  }
}

.main-nav__active-page {
  @media (min-width: @tablet-width) {
    color: @basic-brown;

    &:hover {
      color: @dark-brown;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.main-nav__toggle {
  position: absolute;
  right: 20px;
  width: 22px;
  height: 22px;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.main-nav--opened {
  .main-nav__toggle {
    top: 17px;
    background: url("../img/icon-cross.svg");
  }

  .main-nav__menu-list {
    @media (max-width: @mobile-width-only) {
      display: block;
    }
  }
}

.main-nav--closed {
  .main-nav__toggle {
    bottom: -38px;
    background: url("../img/icon-menu.svg");
  }

  .main-nav__menu-list {
    @media (max-width: @mobile-width-only) {
      display: none;
    }
  }
}

.main-nav--no-js {
  .main-nav__menu-list {
    @media (max-width: @mobile-width-only) {
      display: block;
    }
  }

  .main-nav__toggle {
    display: none;
  }
}
