.video {
  text-align: center;

  @media (max-width: @mobile-width-only) {
    width: 320px;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 35px 0 112px 0;
  }

  @media (min-width: @tablet-width) {
    width: 768px;
    margin: 0 auto;
    padding: 0 60px;
    box-sizing: border-box;
    padding-bottom: 42px;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;
    padding: 0;
    padding-bottom: 91px;
  }
}

.video__title {
  margin: 0;
  font-size: 18px;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 26px;
    margin: 0;
    padding: 57px 20px 36px 20px;
  }

  @media (min-width: @tablet-width) {
    padding: 57px 0 41px 0;
  }

  @media (min-width: @desktop-width) {
    padding: 57px 0 33px 0;
  }
}

.video__text {
  margin: 0;
  line-height: 24px;

  @media (max-width: @mobile-width-only) {
    padding: 32px 30px 33px 30px;
  }

  @media (min-width: @tablet-width) {
    padding: 0 100px;
    margin-bottom: 78px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 370px;
    margin-bottom: 82px;
  }
}
