.page-footer {
  background-color: @basic-white;

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;
    box-shadow: 0 10px 15px 0 @shadow;
  }
}

.page-footer__wrapper {
  @media (max-width: @mobile-width-only) {
    width: 320px;
    margin: 0 auto;
    text-align: center;
    padding-top: 32px;
    padding-bottom: 23px;
  }

  @media (min-width: @tablet-width) {
    padding: 30px 60px 36px 60px;
    box-sizing: border-box;
    width: 768px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;
    padding: 30px 120px 36px 120px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
  }
}

.page-footer__text {
  margin: 0;
  font-size: 21px;

  @media (max-width: @mobile-width-only) {
    margin-bottom: 34px;
  }

  @media (min-width: @tablet-width) {
    //
  }

  @media (min-width: @desktop-width) {
    margin-right: 254px;
  }
}
