.attractions-form {
  @media (max-width: @mobile-width-only) {
    padding-bottom: 30px;
  }

  @media (min-width: @tablet-width) {
    grid-column: ~"1/3";
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 38px;

    &::before {
      content: "";
      position: absolute;
      width: 123px;
      height: 1px;
      background-color: @form-before-grey;
      top: -69px;
      left: 0;
    }

    &::after {
      content: "";
      position: absolute;
      width: 123px;
      height: 1px;
      background-color: @form-before-grey;
      top: -69px;
      right: 0;
    }
  }

  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
    padding-bottom: 66px;

    &::before {
      width: 280px;
      top: -69px;
    }

    &::after {
      width: 280px;
      top: -69px;
    }
  }
}

.attractions-form__title {
  text-align: center;

  @media (max-width: @mobile-width-only) {
    padding: 9px 0 25px 0;
    text-align: center;
  }

  @media (min-width: @tablet-width) {
    padding-bottom: 43px;
  }
}

.checkbox__input + .attractions-form__label {
  position: relative;
  display: block;
  cursor: pointer;
  text-transform: none;
  padding: 13px 0 17px 83px;

  &::before {
    content: "";
    position: absolute;
    width: 56px;
    height: 56px;
    background-color: @light-grey;
    left: 0;
    top: 0;
    cursor: pointer;
  }

  @media (min-width: @desktop-width) {
    padding: 17px 0 17px 82px;
  }
}

.checkbox__input:checked + .attractions-form__label::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 17px;
  border-radius: 50%;
  background: url("../img/icon-tick.svg") no-repeat center;
  left: 15px;
  top: 15px;
}
