.player {
  background: url("../img/video-mobile.jpg") no-repeat center;
  width: 320px;
  height: 200px;
  margin: 0 auto;
  position: relative;

  @media (max-width: @mobile-width-only) and (min-resolution: 2dppx) {
    background-image: url("../img/video-mobile@2x.jpg");
  }

  @media (min-width: @tablet-width) {
    width: 100%;
    height: 500px;
    background-image: url("../img/video-desktop.jpg");
  }

  @media (min-width: @tablet-width) and (min-resolution: 2dppx) {
    background-image: url("../img/video-desktop@2x.jpg");
  }

  @media (min-width: @desktop-width) {
    background-image: url("../img/video-desktop.jpg");
    width: 800px;
  }

  @media (min-width: @desktop-width) and (min-resolution: 2dppx) {
    background-image: url("../img/video-desktop@2x.jpg");
  }
}

.player__panel {
  position: absolute;
  width: 280px;
  height: 45px;
  background-color: @tranparent-black;
  bottom: 0;
  left: 20px;

  @media (min-width: @tablet-width) {
    width: 648px;
    height: 93px;
    left: 0;
  }

  @media (min-width: @desktop-width) {
    width: 800px;
    left: 0;
  }
}

.player__controls {
  position: absolute;
  width: 280px;
  height: 66px;
  background-color: @light-grey;
  bottom: -66px;
  left: 20px;
  display: flex;
  align-items: center;

  @media (min-width: @tablet-width) {
    width: 648px;
    height: 93px;
    left: 0;
    bottom: 0;
    background-color: unset;
  }

  @media (min-width: @desktop-width) {
    width: 800px;
    left: 0;
  }
}

.player__band {
  &::before {
    content: "";
    position: absolute;
    width: 235px;
    height: 3px;
    background-color: @clear-white;
    top: -22px;
    left: 22px;
  }

  &::after {
    content: "";
    position: absolute;
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background-color: @basic-white;
    top: -25px;
    left: 58px;
  }

  @media (min-width: @tablet-width) {
    &::before {
      width: 389px;
      top: 45px;
      left: 93px;
    }

    &::after {
      width: 13px;
      height: 13px;
      top: 41px;
      left: 204px;
    }
  }

  @media (min-width: @desktop-width) {
    &::before {
      width: 540px;
      top: 45px;
      left: 93px;
    }

    &::after {
      width: 13px;
      height: 13px;
      top: 41px;
      left: 204px;
    }
  }
}

.player__icon {
  fill: @basic-white;

  @media (max-width: @mobile-width-only) {
    fill: @control-grey;

    &:hover {
      fill: @dark-text;
    }

    &:active {
      fill: @dark-text;
      opacity: 0.3;
    }
  }

  @media (min-width: @tablet-width) {
    fill: @basic-white;

    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.player__button {
  padding: 0;
  background-color: unset;
}

.player__button--replay {
  margin: 0 133px 0 25px;

  @media (min-width: @tablet-width) {
    margin: 0 473px 0 35px;
  }

  @media (min-width: @desktop-width) {
    margin: 0 624px 0 35px;
  }
}

.player__button--sub {
  margin-right: 31px;
}

.player__button--fullscreen {
  margin-right: 23px;

  @media (min-width: @tablet-width) {
    margin-right: 41px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 42px;
  }
}
