.photos {
  @media (max-width: @mobile-width-only) {
    padding: 0 20px;
    width: 320px;
    margin: 0 auto;
    box-sizing: border-box;
  }

  @media (min-width: @tablet-width) {
    width: 768px;
    margin: 0 auto;
    padding: 0 60px;
    box-sizing: border-box;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;
    padding: 0;
  }
}

.photos__list {
  .reset-list();

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 324px 324px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 400px 400px 400px;
  }

  // @media (min-width: @tablet-width) {
  //   &:first-child {
  //     grid-column: 1/3;
  //   }
  // }
}

.photos__item {
  @media (max-width: @mobile-width-only) {
    padding: 26px 0 15px 0;
    position: relative;

    &:first-child {
      padding: 32px 0 15px 0;
    }

    &::after {
      content: "";
      position: absolute;
      width: 320px;
      height: 2px;
      background-color: @catalog-after-grey;
      bottom: 0;
      left: 50%;
      margin-left: -160px;
    }
  }

  @media (min-width: @tablet-width) {
    &:first-child {
      grid-column-start: 1;
      grid-column-end: 3;
      padding-top: 30px;
    }
  }

  @media (min-width: @desktop-width) {
    &:first-child {
      grid-column-start: 1;
      grid-column-end: 4;
      padding-top: 42px;

      .photos__like-counter {
        @media (min-width: @desktop-width) {
          padding: 28px 21px 27px 0;
        }
      }

      .photos__button {
        @media (min-width: @desktop-width) {
          margin-right: 5px;
        }
      }
    }

    &:nth-child(3) {
      .photos__like-counter {
        @media (min-width: @desktop-width) {
          padding: 28px 37px 27px 0;
        }
      }

      .photos__button {
        @media (min-width: @desktop-width) {
          margin-right: 5px;
        }
      }
    }
  }
}

.photos__container {
  margin: 0;
  position: relative;
}

.photos__text {
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  color: @basic-black;
  margin: 0;

  @media (min-width: @desktop-width) {
    color: @basic-white;
  }
}

.photos__copyright {
  font-size: 14px;
  line-height: 24px;
  text-transform: none;
  margin: 0;
}

.photos__button {
  margin-right: 5px;
}

.photos__like-number {
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  color: @basic-black;

  @media (min-width: @desktop-width) {
    color: @basic-white;
  }
}

.photos__caption {
  @media (max-width: @mobile-width-only) {
    //
  }

  @media (min-width: @tablet-width) {
    background-color: @light-grey;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    background-color: @clear-black;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 80px;
  }
}

.photos__description {
  @media (max-width: @mobile-width-only) {
    padding-top: 10px;
  }

  @media (min-width: @tablet-width) {
    padding: 22px 0 15px 20px;
  }

  @media (min-width: @desktop-width) {
    color: @basic-white;
    padding: 13px 0 15px 20px;
  }
}

.photos__like-counter {
  @media (max-width: @mobile-width-only) {
    padding-top: 10px;
  }

  @media (min-width: @tablet-width) {
    padding: 28px 20px 26px 0;
  }

  @media (min-width: @desktop-width) {
    padding: 27px 29px 26px 0;
  }
}

.photos__picture {
  width: 100%;
  display: block;
  box-sizing: border-box;
  object-fit: cover;

  @media (max-width: @mobile-width-only) {
    height: 280px;
  }
}
