.copyright {
  display: flex;
  justify-content: center;
  align-items: center;

  @media (min-width: @tablet-width) {
    margin-left: 7px;
  }

  @media (min-width: @desktop-width) {
    margin-left: 261px;
  }
}

.copyright__text {
  margin: 0;
  margin-right: 12px;
  line-height: 26px;
}

.copyright__link {
  display: block;
  height: 42px;
}

.copyright__icon {
  &:hover {
    fill: @light-blue;
  }

  &:active {
    fill: @basic-black;
    opacity: 0.3;
  }
}
