.introduce-form {
  @media (max-width: @mobile-width-only) {
    //
  }

  @media (min-width: @tablet-width) {
    padding: 0 0 70px 58px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 2px;
      height: 292px;
      background-color: @form-before-grey;
      top: -77px;
      left: 0;
    }
  }

  @media (min-width: @desktop-width) {
    padding: 0 0 70px 79px;
  }
}

.introduce-form__title {
  @media (max-width: @mobile-width-only) {
    padding: 6px 0 19px 0;
    text-align: center;
  }

  @media (min-width: @tablet-width) {
    padding: 35px 0 55px 0;
  }

  @media (min-width: @desktop-width) {
    padding: 20px 0 55px 0;
  }
}

.introduce-form__label {
  margin-right: 13px;
}

.introduce-form__item {
  display: flex;
  margin-bottom: 20px;
  align-items: center;

  @media (max-width: @mobile-width-only) {
    &:last-of-type {
      margin-bottom: 25px;
    }
  }

  @media (min-width: @tablet-width) {
    &:last-of-type {
      margin-bottom: 0;
    }
  }
}
