.review {
  line-height: 26px;
  margin: 0 auto;
  box-sizing: border-box;

  @media (max-width: @mobile-width-only) {
    padding: 0 20px;
    width: 320px;
  }

  @media (min-width: @tablet-width) {
    padding: 0 60px;
    width: 768px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 5px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 120px;
    width: 1200px;
    padding-bottom: 10px;
  }
}

.review__item {
  border: none;
  margin: 0;
  padding: 0;
  position: relative;

  @media (max-width: @mobile-width-only) {
    &:not(:last-of-type)::after {
      content: "";
      position: absolute;
      width: 320px;
      height: 2px;
      background-color: @catalog-after-grey;
      bottom: 0;
      left: 50%;
      margin-left: -160px;
    }

    &:first-of-type::before {
      content: "";
      position: absolute;
      width: 320px;
      height: 2px;
      background-color: @catalog-after-grey;
      top: -69px;
      left: 50%;
      margin-left: -160px;
    }
  }
}

.review__button {
  background-color: @light-blue;

  @media (max-width: @mobile-width-only) {
    padding: 15px 32px 18px 32px;
    margin-bottom: 44px;
  }

  @media (min-width: @tablet-width) {
    padding: 18px 60px 14px 49px;
    margin-bottom: 61px;
  }
}

.review__requiring {
  margin-left: auto;
  padding-top: 19px;

  @media (max-width: @mobile-width-only) {
    display: none;
  }
}
