.radio {
  @media (max-width: @mobile-width-only) {
    &:not(:last-child) {
      margin-bottom: 13px;
    }
  }

  @media (min-width: @tablet-width) {
    &:not(:last-child) {
      margin-bottom: 22px;
    }
  }
}

.radio__label {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  font-style: normal;
  color: @basic-black;
}
