.impression-form {
  @media (max-width: @mobile-width-only) {
    padding-bottom: 31px;
  }

  @media (min-width: @tablet-width) {
    order: -1;
  }
}

.impression-form__title {
  @media (max-width: @mobile-width-only) {
    padding: 20px 0 25px 0;
    text-align: center;
  }

  @media (min-width: @tablet-width) {
    padding: 35px 0 59px 0;
  }

  @media (min-width: @tablet-width) {
    padding: 21px 48px 59px 0;
    text-align: center;
  }
}

.radio__checkbox + .impression-form__label {
  position: relative;
  display: block;
  cursor: pointer;
  padding: 13px 0 17px 76px;
  text-transform: none;

  &::before {
    content: "";
    position: absolute;
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background-color: @light-grey;
    left: 0;
    top: 0;
    cursor: pointer;
  }
}

.radio__checkbox:checked + .impression-form__label::after {
  content: "";
  position: absolute;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: @basic-black;
  left: 19px;
  top: 19px;
}
