.checkbox {
  @media (max-width: @mobile-width-only) {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 32px;

    &:nth-of-type(odd) {
      margin-left: 100px;
    }

    &:nth-of-type(even) {
      margin-left: 29px;
    }
  }

  @media (min-width: @desktop-width) {
    margin: 0;

    &:nth-of-type(odd) {
      margin-left: 0;
    }

    &:nth-of-type(even) {
      margin-left: 0;
    }
  }
}

.checkbox__label {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  font-style: normal;
  color: @basic-black;
}
