.city-advantages {
  @media (max-width: @mobile-width-only) {
    width: 320px;
    margin: 0 auto;
    padding: 35px 20px 9px 20px;
    box-sizing: border-box;
  }

  @media (min-width: @tablet-width) {
    width: 768px;
    margin: 0 auto;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;
  }
}

.city-advantages__list {
  .reset-list();
  display: flex;

  @media (max-width: @mobile-width-only) {
    padding-left: 51px;
    flex-direction: column;
    // width: 320px;
    // margin: 0 auto;
  }

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.city-advantages__item {
  @media (min-width: @tablet-width) {
    padding: 158px 31px 38px 33px;
  }

  @media (min-width: @desktop-width) {
    padding: 160px 31px 57px 33px;
  }
}

.city-advantages__item--house {
  &::before {
    background: url("../img/icon-house.svg") no-repeat center;
  }
}

// .city-advantages__item--eat {
//   order: 2;

//   &::before {
//     background: url("../img/icon-burger.svg") no-repeat center;
//   }
// }

.city-advantages__item--souvenirs {
  &::before {
    background: url("../img/icon-gift.svg") no-repeat center;
  }
}

.city-advantages__item--house,
.city-advantages__item--souvenirs {
  display: flex;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    // justify-content: center;
    // align-items: center;
    position: relative;

    &::before {
      content: "";
      width: 75px;
      height: 75px;
      background-size: contain;
      position: absolute;
      top: 60px;
      left: 50%;
      margin-left: -37.5px;
    }
  }


  @media (max-width: @mobile-width-only) {
    position: relative;

    &::before {
      content: "";
      width: 40px;
      height: 38px;
      background-size: contain;
      position: absolute;
      top: -12px;
      left: -50px;
    }
  }
}

.city-advantages__item--eat {
  order: 2;

  &::before {
    background: url("../img/icon-burger.svg") no-repeat center;
  }

  @media (max-width: @mobile-width-only) {
    position: relative;

    &::before {
      content: "";
      width: 40px;
      height: 38px;
      background-size: contain;
      position: absolute;
      top: -7px;
      left: -52px;
    }
  }

  @media (min-width: @tablet-width) {
    justify-content: center;
    align-items: center;
    position: relative;
    order: 0;

    &::before {
      content: "";
      width: 75px;
      height: 75px;
      background-size: contain;
      position: absolute;
      top: 60px;
      left: 50%;
      margin-left: -37.5px;
    }
  }
}

.city-advantages__title {
  margin: 0;
  margin-bottom: 19px;

  @media (min-width: @tablet-width) {
    text-align: center;
    margin-bottom: 23px;
  }
}

.city-advantages__description {
  margin: 0;
  margin-bottom: 22px;
  text-align: center;

  @media (max-width: @mobile-width-only) {
    text-align: start;
    margin-bottom: 23px;
  }
}
