.emotions-form {
  @media (max-width: @mobile-width-only) {
    padding-bottom: 44px;
  }

  @media (min-width: @tablet-width) {
    grid-column: ~"1/3";
    padding-bottom: 49px;

    &::before {
      content: "";
      position: absolute;
      width: 123px;
      height: 1px;
      background-color: @form-before-grey;
      top: 10px;
      left: 0;
    }

    &::after {
      content: "";
      position: absolute;
      width: 123px;
      height: 1px;
      background-color: @form-before-grey;
      top: 10px;
      right: 0;
    }
  }

  @media (min-width: @desktop-width) {
    &::before {
      width: 280px;
      top: 10px;
    }

    &::after {
      width: 280px;
      top: 10px;
    }
  }
}

.emotions-form__title {
  text-align: center;
  display: block;

  @media (max-width: @mobile-width-only) {
    padding: 10px 0 15px 0;
  }

  @media (min-width: @tablet-width) {
    padding-bottom: 55px;
  }
}

.emotions-form__text-area {
  font-family: "PT sans", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  font-style: normal;
  resize: none;
  min-height: 160px;
  padding: 13px 23px 29px 21px;
  color: @basic-black;
  border: none;
  background-color: @light-grey;
  width: 100%;
  box-sizing: border-box;

  &::placeholder {
    color: @basic-black;
  }

  @media (min-width: @tablet-width) {
    padding: 17px 23px 29px 21px;
  }
}
