.advantages {
  position: relative;

  @media (max-width: @mobile-width-only) {
    &::before {
      content: "";
      width: 100%;
      height: 37px;
      background: url("../img/outro-triangle-mobile.svg") no-repeat center;
      position: absolute;
      top: -22px;
      left: 50%;
      margin-left: -50%;
    }
  }

  @media (min-width: @tablet-width) {
    width: 768px;
    margin: 0 auto;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;
  }
}

.advantages__list {
  .reset-list();
}

.advantages__group-items {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.advantages__item {
  text-align: center;
}

.advantages__container {
  @media (max-width: @mobile-width-only) {
    width: 320px;
    margin: 0 auto;
  }

  @media (min-width: @tablet-width) {
    padding: 0 32px 0 32px;
  }

  @media (min-width: @desktop-width) {
    padding: 47px 55px 0 55px;
  }
}

.advantages__item--theme_blue {
  background-color: @light-blue;
  color: @basic-white;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 2fr;

    &:nth-of-type(2) {
      grid-template-columns: 2fr 1fr;
    }
  }
}

.advantages__item--theme-grey {
  background-color: @grey;

  @media (max-width: @mobile-width-only) {
    position: relative;
    padding: 27px 35px 25px 35px;

    &:not(:last-child)::after {
      content: "";
      position: absolute;
      width: 280px;
      height: 2px;
      background-color: @after-grey;
      bottom: 0;
      left: 50%;
      margin-left: -140px;
    }
  }

  @media (min-width: @tablet-width) {
    padding: 41px 33px 41px 34px;
  }

  @media (min-width: @desktop-width) {
    padding: 44px 43px 61px 42px;
  }
}

.advantages__title {
  margin: 0;
  padding: 0;
  margin-bottom: 12px;
}

.advantages__title--theme-blue {
  padding-top: 50px;

  @media (min-width: @tablet-width) {
    padding-top: 40px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 0;
  }
}

.advantages__subtitle--theme_black,
.advantages__subtitle--theme_blue {
  position: relative;

  &::after {
    content: "";
    width: 8px;
    height: 1px;
    background-color: @basic-black;
    position: absolute;
    top: 8px;
    right: -13px;
  }

  &::before {
    content: "";
    width: 8px;
    height: 1px;
    background-color: @basic-black;
    position: absolute;
    top: 8px;
    left: -13px;
  }
}

.advantages__subtitle--theme_blue {
  &::after {
    background-color: @basic-white;
  }

  &::before {
    background-color: @basic-white;
  }
}

.advantages__description {
  margin: 0;
  padding-top: 25px;

  @media (max-width: @mobile-width-only) {
    padding-top: 15px;
  }
}

.advantages__description--theme-blue {
  @media (max-width: @mobile-width-only) {
    padding-bottom: 39px;
  }

  @media (min-width: @tablet-width) {
    padding: 25px 0 39px 0;
  }
}

.advantages__picture {
  width: 100%;
  display: block;
  box-sizing: border-box;
  object-fit: cover;

  @media (max-width: @mobile-width-only) {
    height: 260px;
  }
}

.advantages__item--theme_blue:last-of-type {
  .advantages__title--theme-blue {
    @media (max-width: @mobile-width-only) {
      padding-top: 40px;
    }


    @media (min-width: @tablet-width) {
      padding-top: 44px;
    }
  }

  .advantages__description--theme-blue {
    @media (max-width: @mobile-width-only) {
      padding-bottom: 35px;
    }
  }

  .advantages__container {
    @media (min-width: @tablet-width) {
      order: 2;
    }

    @media (min-width: @desktop-width) {
      padding: 8px 90px 0 90px;
    }
  }
}

.advantages__title--br {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
  }
}

.advantages__description--br {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
  }

  @media (min-width: @desktop-width) {
    &:first-child {
      display: none;
    }
  }
}

.city-advantages__description--br {
  display: none;

  @media (min-width: @tablet-width) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    display: block;
  }
}
