// header styles
.page-header {
  position: relative;
  background-color: @basic-white;
  // @media (min-width: @tablet-width) {
  //   width: 768px;
  //   margin: 0 auto;
  // }

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;
    box-shadow: 0 -10px 15px 0 @shadow;
  }
}

.page-header__logo {
  // width: 102px;
  // height: 84px;
  // margin: 0 auto;
  // display: block;
  position: absolute;
  left: 50%;
  margin-left: -51px;
  z-index: 1;

  @media (min-width: @tablet-width) {
    top: 0;
  }

  @media (min-width: @desktop-width) {
    margin-left: -94px;
  }
}

.page-header__logo img {
  @media (min-width: @desktop-width) {
    width: 188px;
    height: 97px;
  }
}

.page-header__banner {
  background: @basic-white url("../img/back-mobile.jpg") no-repeat center;
  background-size: cover;
  text-align: center;
  position: relative;

  @media (max-width: @mobile-width-only) {
    margin-top: 56px;
  }

  @media (max-width: @mobile-width-only) and (min-resolution: 2dppx) {
    background-image: url("../img/back-mobile@2x.jpg");
  }

  @media (min-width: @tablet-width) {
    background-image: url("../img/back-desktop.jpg");
  }

  @media (min-width: @tablet-width) and (min-resolution: 2dppx) {
    background-image: url("../img/back-desktop@2x.jpg");
  }

  &::after {
    content: "";
    width: 100%;
    height: 58px;
    background: url("../img/intro-triangle123.svg") no-repeat center;
    position: absolute;
    bottom: -1px;
    left: 50%;
    margin-left: -50%;
  }
}

.page-header__banner--main {
  padding: 90px 0 76px 0;

  @media (min-width: @tablet-width) {
    padding: 80px 0 71px 0;
  }
}

.page-header__banner--form {
  padding: 52px 0 59px 0;

  @media (min-width: @tablet-width) {
    padding: 95px 0 110px 0;
  }

  @media (min-width: @desktop-width) {
    padding: 100px 0 114px 0;
  }
}


.page-header__slogan {
  @media (min-width: @tablet-width) {
    width: 600px;
    height: 360px;
  }
}

.page-header__slogan--catalog {
  @media (min-width: @tablet-width) {
    width: 457px;
    height: 78px;
  }

  @media (min-width: @desktop-width) {
    width: 460px;
    height: 80px;
  }
}
